import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { FetchClient, IApplication, IFetchOptions, IFetchResponse } from '@c8y/client';
import { PropertiesListItem, WizardService } from '@c8y/ngx-components';
import { lexer as markedLexer, parser as markedParser } from 'marked';
import { ModalOptions } from 'ngx-bootstrap/modal';
import { packageProperties } from '../../ecosystem.constants';
import { ApplicationState } from '../../ecosystem.model';
import { EcosystemService } from '../../ecosystem.service';

@Component({
  selector: 'c8y-package-details',
  templateUrl: './package-details.component.html'
})
export class PackageDetailsComponent implements OnInit {
  readme: string;
  package: IApplication = {};
  name: string;
  description: string;
  appState: ApplicationState;
  isApplicationPackage: boolean;

  readonly packageProperties: PropertiesListItem[] = packageProperties;
  private readonly headers: any = { 'Content-Type': 'text/markdown', responseType: 'blob' };
  private NOT_FOUND_ERROR_CODE = 404;

  constructor(
    private activatedRoute: ActivatedRoute,
    private client: FetchClient,
    private wizard: WizardService,
    private ecosystemService: EcosystemService
  ) {}

  async ngOnInit() {
    this.package = this.getPackage();
    this.name = this.package?.name;
    this.description = this.package?.manifest?.description;
    this.readme = await this.getReadmeFileContentAsHtml();
    this.appState = this.ecosystemService.getAppState(this.package);
    this.isApplicationPackage = this.ecosystemService.isApplicationPackage(this.package);
  }

  deploy() {
    const initialState: any = {
      wizardConfig: {},
      id: 'deployPackage',
      package: this.package
    };
    const modalOptions: ModalOptions = { initialState };
    this.wizard.show(modalOptions);
  }

  private getPackage() {
    return this.activatedRoute?.snapshot?.parent?.data?.contextData;
  }

  private async getReadmeFileContentAsHtml(): Promise<string> {
    const readmeFile = await this.getReadmeFile();

    const readmeContent = await readmeFile.text();
    if (readmeFile.status === 200) {
      const tokens = markedLexer(readmeContent);
      const html = markedParser(tokens);
      return html;
    }
  }

  private async getReadmeFile(): Promise<IFetchResponse> {
    let result: IFetchResponse;
    const options: IFetchOptions = {
      method: 'GET',
      headers: this.headers
    };

    result = await this.client.fetch(`/apps/${this.package.contextPath}/readme.md`, options);

    if (result && result.status === this.NOT_FOUND_ERROR_CODE) {
      result = await this.client.fetch(`/apps/${this.package.contextPath}/README.md`, options);
    }
    return result;
  }
}
