import { Injectable } from '@angular/core';
import { ActivatedRouteSnapshot, CanActivate } from '@angular/router';
import { EcosystemService } from '../ecosystem.service';

@Injectable()
export class ApplicationPropertiesGuard implements CanActivate {
  constructor(private ecosystemService: EcosystemService) {}

  canActivate(route: ActivatedRouteSnapshot) {
    const app = route.data.contextData || route.parent.data.contextData;
    if (!app) {
      return false;
    }
    return !this.ecosystemService.isPackage(app);
  }
}
