import { Component, OnInit } from '@angular/core';
import { IApplication, IResultList } from '@c8y/client';
import { WizardService } from '@c8y/ngx-components';
import { ModalOptions } from 'ngx-bootstrap/modal';
import { BehaviorSubject, Observable } from 'rxjs';
import { shareReplay, switchMap, tap } from 'rxjs/operators';
import { EcosystemService } from '../ecosystem.service';

@Component({
  selector: 'c8y-microservice-list',
  templateUrl: './microservice-list.component.html'
})
export class MicroserviceListComponent implements OnInit {
  reloading: boolean = false;
  reload$: BehaviorSubject<void> = new BehaviorSubject(null);
  microservices$: Observable<IApplication> = this.reload$.pipe(
    tap(() => (this.reloading = true)),
    switchMap(() => this.ecosystemService.getMicroservices()),
    tap(() => (this.reloading = false)),
    shareReplay()
  );
  isMicroserviceHostingAllowed$: Observable<boolean> = this.reload$.pipe(
    switchMap(() => this.ecosystemService.isMicroserviceHostingAllowed()),
    shareReplay()
  );
  listClass: string;

  constructor(
    private ecosystemService: EcosystemService,
    private wizardService: WizardService
  ) {}

  ngOnInit() {
    this.loadMicroservices();
  }

  loadMicroservices() {
    this.reload$.next();
  }

  addMicroservice() {
    const initialState: any = {
      id: 'uploadMicroservice'
    };
    const modalOptions: ModalOptions = { initialState };

    const modalRef = this.wizardService.show(modalOptions);
    modalRef.content.onClose.subscribe(() => {
      this.loadMicroservices();
    });
  }
}
