import { Component, Input, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { IApplication } from '@c8y/client';

@Component({
  selector: 'c8y-application-properties-form',
  templateUrl: './application-properties-form.component.html'
})
export class ApplicationPropertiesFormComponent implements OnInit {
  formGroup: FormGroup;
  @Input() application: IApplication;
  @Input() isPackage: boolean;

  constructor(private formBuilder: FormBuilder) {}

  async ngOnInit() {
    this.initForm();
    this.setForm(this.application);
  }

  private initForm() {
    this.formGroup = this.formBuilder.group({
      name: [Validators.required, Validators.maxLength(120)],
      key: [Validators.required, Validators.maxLength(120)],
      contextPath: [Validators.required, Validators.maxLength(120)]
    });
  }

  private setForm(applicationConfig: IApplication) {
    this.formGroup.patchValue({ ...applicationConfig });
  }
}
