import { Component } from '@angular/core';
import { ApplicationType, IApplication } from '@c8y/client';
import { EcosystemService } from '../ecosystem.service';
import { gettext } from '@c8y/ngx-components';

@Component({
  selector: 'c8y-add-package',
  template: `<c8y-add-application
    [headerText]="headerText"
    [headerIcon]="'big-parcel'"
    [successText]="successText"
    [createApplicationHandler]="createPackageAppHandler"
    [uploadApplicationHandler]="uploadPackageHandler"
  ></c8y-add-application>`
})
export class AddPackageComponent {
  headerText: string = gettext('Add package');
  successText: string = gettext('Package created');

  constructor(private ecosystemService: EcosystemService) {}

  createPackageAppHandler = (f: File) => this.createPackageApp(f);
  uploadPackageHandler = (f: File, app: IApplication) => this.uploadPackage(f, app);

  async createPackageApp(file: File): Promise<IApplication> {
    await this.ecosystemService.isValidAppType(file, ApplicationType.HOSTED);
    return this.ecosystemService.createAppForArchive(file, true);
  }

  uploadPackage(file: File, pckg: IApplication): Promise<IApplication> {
    return this.ecosystemService.uploadArchiveToApp(file, pckg);
  }
}
