import { Component, EventEmitter, Input, Output, ViewChild } from '@angular/core';
import { ApplicationService, IApplication } from '@c8y/client';
import { C8yStepper } from '@c8y/ngx-components';
import { BsModalRef } from 'ngx-bootstrap/modal';
import { BehaviorSubject } from 'rxjs';
import { EcosystemService } from '../../../ecosystem.service';
import { ApplicationPropertiesFormComponent } from '../../../shared/application-properties-form.component';

@Component({
  selector: 'c8y-duplicate-application-properties',
  templateUrl: './duplicate-application-properties.component.html'
})
export class DuplicateApplicationPropertiesComponent {
  @Input() stepper: C8yStepper;
  @Input() existingApps: IApplication[];
  @Input() selectedApp: BehaviorSubject<IApplication>;
  @Output() duplicatedApp = new EventEmitter<IApplication>();
  @ViewChild(ApplicationPropertiesFormComponent)
  applicationPropertiesForm: ApplicationPropertiesFormComponent;
  newAppConfig: IApplication;

  inProgress: boolean = false;

  constructor(
    private bsModalRef: BsModalRef,
    private ecosystemService: EcosystemService,
    private applicationService: ApplicationService
  ) {}

  ngOnInit() {
    this.newAppConfig = this.ecosystemService.getUniqueAppConfig(
      this.selectedApp.value,
      this.existingApps
    );
  }

  async duplicateApp() {
    const formGroupValue = this.applicationPropertiesForm.formGroup.getRawValue();
    this.inProgress = true;
    const clonedApp = (await this.applicationService.clone(this.selectedApp.value)).data;

    Object.assign(clonedApp, formGroupValue);
    delete clonedApp.type;

    await this.updateApp(clonedApp);
    this.inProgress = false;
  }

  cancel() {
    this.bsModalRef.hide();
  }

  back() {
    this.stepper.previous();
  }

  private async updateApp(clonedAppConfig: Partial<IApplication>) {
    try {
      const app = (await this.ecosystemService.updateApp(clonedAppConfig, true)).data;
      this.duplicatedApp.emit(app);
      this.stepper.next();
    } catch (err) {
      this.inProgress = false;
    }
  }
}
