import { Injectable } from '@angular/core';
import { gettext, Tab, TabFactory } from '@c8y/ngx-components';
import { Router } from '@angular/router';
import { EcosystemGuard } from './ecosystem.guard';

@Injectable()
export class EcosystemTabs implements TabFactory {
  constructor(public router: Router, private ecosystemGuard: EcosystemGuard) {}

  get(): Tab[] {
    const tabs: Tab[] = [];
    if (
      this.router.url === '/ecosystem/applications' ||
      this.router.url === '/ecosystem/package-list' ||
      this.router.url === '/ecosystem/features'
    ) {
      tabs.push({
        icon: 'c8y-modules',
        priority: 1000,
        label: gettext('All applications'),
        path: 'ecosystem/applications'
      } as Tab);

      if (this.ecosystemGuard.isFeatureEnabled()) {
        tabs.push({
          icon: 'big-parcel',
          priority: 999,
          label: gettext('Packages'),
          path: 'ecosystem/package-list'
        } as Tab);
      }

      tabs.push({
        icon: 'tab',
        priority: 998,
        label: gettext('Features'),
        path: '/ecosystem/features'
      } as Tab);
    }
    return tabs;
  }
}
