import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ApplicationService, ApplicationType, IApplication, IManifest } from '@c8y/client';
import { AlertService, WizardComponent } from '@c8y/ngx-components';

@Component({
  selector: 'c8y-add-external-application',
  templateUrl: './add-external-application.component.html'
})
export class AddExternalApplicationComponent implements OnInit {
  formGroup: FormGroup;
  createdApp: IApplication;

  constructor(
    private applicationService: ApplicationService,
    private formBuilder: FormBuilder,
    private alertService: AlertService,
    private wizardComponent: WizardComponent
  ) {}

  ngOnInit() {
    this.initForm();
  }

  back() {
    this.wizardComponent.reset();
  }

  cancel() {
    this.wizardComponent.close();
  }

  done() {
    this.wizardComponent.close();
  }

  async save(app) {
    const appToSave = {
      manifest: {} as IManifest,
      externalUrl: app.externalUrl,
      type: ApplicationType.EXTERNAL,
      name: app.name,
      key: app.key
    };
    try {
      this.createdApp = await this.applicationService.create(appToSave);
    } catch (ex) {
      this.alertService.addServerFailure(ex);
    }
  }

  private initForm(): void {
    this.formGroup = this.formBuilder.group({
      name: ['', [Validators.required, Validators.maxLength(120)]],
      key: ['', [Validators.required, Validators.maxLength(120)]],
      externalUrl: ['', Validators.required]
    });
  }
}
