import { Component, Input, OnInit } from '@angular/core';
import { IApplication, IApplicationBinary } from '@c8y/client';
import { AlertService } from '@c8y/ngx-components';
import { BehaviorSubject } from 'rxjs';
import { EcosystemService } from '../ecosystem.service';

@Component({
  selector: 'c8y-activity-log',
  templateUrl: './activity-log.component.html'
})
export class ActivityLogComponent implements OnInit {
  @Input() application: IApplication;
  archives: IApplicationBinary[] = [];
  toActivateVersionId: string;
  last: IApplicationBinary;
  isLoading: boolean;
  canReactivate: boolean = false;

  constructor(private ecosystemService: EcosystemService, private alertService: AlertService) {}

  get uploadProgress(): BehaviorSubject<number> {
    return this.ecosystemService.progress;
  }

  async ngOnInit() {
    this.canReactivate = this.showReactivate();
    this.refresh();
  }

  isActive(archive: IApplicationBinary): boolean {
    return this.application.activeVersionId === archive.id;
  }

  toActivate(archive: IApplicationBinary): boolean {
    return this.toActivateVersionId === archive.id;
  }

  checkIfLast(archive: IApplicationBinary): boolean {
    return archive.id === this.last.id;
  }

  showReactivate(): boolean {
    return this.ecosystemService.isApplication(this.application);
  }

  async setActive(archive): Promise<void> {
    const id = archive.id || archive;
    this.toActivateVersionId = id;
    this.isLoading = true;
    try {
      this.application = (
        await this.ecosystemService.setAppActiveVersion(this.application, id)
      ).data;
    } catch (ex) {
      this.alertService.addServerFailure(ex);
    }
    this.isLoading = false;
    this.refresh();
  }

  async deleteArchive(archive: IApplicationBinary): Promise<void> {
    await this.ecosystemService.deleteArchive(archive, this.application);
    this.refresh();
  }

  async downloadArchive(archive: IApplicationBinary): Promise<void> {
    await this.ecosystemService.downloadArchive(this.application, archive);
  }

  async reactivateArchive(): Promise<void> {
    await this.ecosystemService.reactivateArchive(this.application);
  }

  async onRefresh() {
    await this.refresh();
  }

  private async refresh(): Promise<void> {
    this.isLoading = true;
    this.archives = await this.ecosystemService.listArchives(this.application.id);
    this.archives.sort((a, b) => {
      return (new Date(b.created) as any) - (new Date(a.created) as any);
    });
    this.last = this.archives[this.archives.length - 1];
    this.isLoading = false;
  }
}
