import { Component, OnInit, ViewChild } from '@angular/core';
import { ApplicationService, IApplication } from '@c8y/client';
import { gettext, WizardComponent } from '@c8y/ngx-components';
import { TranslateService } from '@ngx-translate/core';
import { EcosystemService } from '../../ecosystem.service';
import { ApplicationPropertiesFormComponent } from '../../shared/application-properties-form.component';

@Component({
  selector: 'c8y-deploy-application',
  templateUrl: './deploy-application.component.html'
})
export class DeployApplicationComponent implements OnInit {
  inProgress: boolean = true;
  package: IApplication;
  isDeployed: boolean = false;
  deployedWithSuccess: boolean = false;
  newAppConfig: IApplication;

  @ViewChild(ApplicationPropertiesFormComponent)
  applicationPropertiesForm: ApplicationPropertiesFormComponent;

  readonly descriptionTemplate: string = gettext(
    'Deploy application using "{{ packageName }}" package'
  );
  headerText: string;

  constructor(
    private ecosystemService: EcosystemService,
    private applicationService: ApplicationService,
    private wizardComponent: WizardComponent,
    private translate: TranslateService
  ) {}

  async ngOnInit() {
    this.package = (this.wizardComponent as any).package;
    const apps = (await this.ecosystemService.getApplications()).data;
    this.newAppConfig = this.ecosystemService.getUniqueAppConfig(this.package, apps);
    this.headerText = this.getHeaderText();
    this.inProgress = false;
  }

  async deployApp() {
    const formGroupValue = this.applicationPropertiesForm.formGroup.getRawValue();

    this.inProgress = true;

    const { data: clonedPkg } = await this.applicationService.clone(this.package);
    const config = this.ecosystemService.createConfig(clonedPkg, formGroupValue);

    try {
      const { data: updatedApp } = await this.ecosystemService.updateApp(config, true);

      if (updatedApp) {
        await this.ecosystemService.updateAppManifest(updatedApp, this.package);
      }
      this.deployedWithSuccess = true;
    } catch (error) {
      this.markAsDeployed();
      await this.applicationService.delete(clonedPkg.id);
    }
    this.markAsDeployed();
  }

  cancel() {
    this.wizardComponent.close();
  }

  private markAsDeployed() {
    this.isDeployed = true;
    this.inProgress = false;
  }

  private getHeaderText() {
    return this.translate.instant(this.descriptionTemplate, {
      packageName: this.package.name
    });
  }
}
