import { Component, EventEmitter, Input, Output } from '@angular/core';
import { BehaviorSubject } from 'rxjs';
import { ApplicationPlugin } from '../../ecosystem.model';

@Component({
  selector: 'c8y-plugin-list',
  templateUrl: './plugin-list.component.html'
})
export class PluginListComponent {
  @Input() plugins$: BehaviorSubject<ApplicationPlugin[]>;
  @Input() emptyListText: string = '';
  @Input() selectable: boolean;
  @Output() selectedItems: EventEmitter<string[]> = new EventEmitter();
  selectedPlugins: object = {};

  updateSelectedItems(value: boolean, name: string) {
    this.selectedPlugins[name] = value;
    this.selectedItems.emit(
      Object.keys(this.selectedPlugins).filter((k) => this.selectedPlugins[k])
    );
  }
}
