import { Component, Input, OnInit } from '@angular/core';
import { ModalService, gettext, Status } from '@c8y/ngx-components';
import { ApplicationPlugin } from '../../ecosystem.model';
import { BehaviorSubject, combineLatest, Observable } from 'rxjs';
import { BsModalRef } from 'ngx-bootstrap/modal';
import { map } from 'rxjs/operators';

@Component({
  selector: 'c8y-install-plugin',
  templateUrl: './install-plugin.component.html'
})
export class InstallPluginComponent implements OnInit {
  @Input() plugins$: BehaviorSubject<ApplicationPlugin[]>;
  filterTerm$: BehaviorSubject<string> = new BehaviorSubject('');
  filteredPlugins$: Observable<ApplicationPlugin[]> = new BehaviorSubject([]);
  selectedPlugins: string[] = [];
  result: Promise<void> = new Promise((resolve, reject) => {
    this._install = resolve;
    this._cancel = reject;
  });
  private _install;
  private _cancel;

  constructor(private bsModalRef: BsModalRef, private modal: ModalService) {}

  ngOnInit() {
    this.filteredPlugins$ = combineLatest(this.plugins$, this.filterTerm$).pipe(
      map(([plugins, filterTerm]) =>
        filterTerm.trim().length === 0
          ? plugins
          : plugins.filter((plugin: ApplicationPlugin) =>
              this.filterContainString(plugin.name, filterTerm)
            )
      )
    );
  }

  cancel() {
    this.bsModalRef.hide();
    this._cancel();
  }

  async install() {
    await this.modal.confirm(
      gettext('Install plugins'),
      gettext(
        'The operation may take several minutes. During this time the application may be unavailable. Do you want to proceed?'
      ),
      Status.INFO,
      { ok: gettext('Continue') }
    );
    this._install(this.selectedPlugins);
    this.bsModalRef.hide();
  }

  private filterContainString(name: string, filterTerm: string) {
    const term = filterTerm.toLowerCase().trim();
    return name && name.toLowerCase().indexOf(term) > -1;
  }
}
