import { Component, OnInit, ViewChild } from '@angular/core';
import { ApplicationService, IApplication } from '@c8y/client';
import { WizardComponent } from '@c8y/ngx-components';
import { EcosystemService } from '../../ecosystem.service';
import { ApplicationPropertiesFormComponent } from '../../shared/application-properties-form.component';

@Component({
  selector: 'c8y-install-from-package',
  templateUrl: './install-from-package.component.html'
})
export class InstallFromPackageComponent implements OnInit {
  newAppConfig: IApplication;
  selectedPackage: IApplication;
  packages: IApplication[];
  inProgress: boolean;
  deployedWithSuccess: boolean = false;
  isDeployed: boolean = false;

  @ViewChild(ApplicationPropertiesFormComponent)
  applicationPropertiesForm: ApplicationPropertiesFormComponent;

  constructor(
    private ecosystemService: EcosystemService,
    private applicationService: ApplicationService,
    private wizardComponent: WizardComponent
  ) {}

  async ngOnInit() {
    this.loadPackages();
  }

  back() {
    this.wizardComponent.reset();
  }

  cancel() {
    this.wizardComponent.close();
  }

  async save() {
    const formGroupValue = this.applicationPropertiesForm.formGroup.getRawValue();
    this.inProgress = true;

    const { data: clonedPkg } = await this.applicationService.clone(this.selectedPackage);
    const config = this.ecosystemService.createConfig(clonedPkg, formGroupValue);

    try {
      const { data: updatedApp } = await this.ecosystemService.updateApp(config, true);

      if (updatedApp) {
        await this.ecosystemService.updateAppManifest(updatedApp, this.selectedPackage);
      }
      this.deployedWithSuccess = true;
    } catch (error) {
      this.inProgress = false;
      await this.applicationService.delete(clonedPkg.id);
    }
    this.markAsDeployed();
  }

  async selectPackage(selectedPackage: IApplication) {
    const apps = await this.ecosystemService.getWebApplications();
    this.newAppConfig = this.ecosystemService.getUniqueAppConfig(selectedPackage, apps);
    this.selectedPackage = selectedPackage;
  }
  /* The 'select package version' option will be added later when BE support for multiple app versions has been implemented. MTM-40278
  // onSelectedArchive(archive) {
  //   this.formGroup.controls['packageVersion'].setValue(archive.name);
  // } */

  private markAsDeployed() {
    this.isDeployed = true;
    this.inProgress = false;
  }

  private async loadPackages(): Promise<void> {
    const applications = await this.ecosystemService.getApplications();
    this.packages = applications.data.filter((app) =>
      this.ecosystemService.isApplicationPackage(app)
    );
  }
}
