import { Component, OnInit } from '@angular/core';
import { BehaviorSubject, combineLatest, Observable } from 'rxjs';
import { ApplicationRemotePlugins, IApplication, IApplicationBinary, IManifest } from '@c8y/client';
import { ActivatedRoute } from '@angular/router';
import { EcosystemService } from '../../ecosystem.service';
import { ApplicationPlugin } from '../../ecosystem.model';
import { BsModalService } from 'ngx-bootstrap/modal';
import { InstallPluginComponent } from './install-plugin.component';
import { AlertService, gettext, ModalService, PluginsService, Status } from '@c8y/ngx-components';
import { TranslateService } from '@ngx-translate/core';
import { map, shareReplay } from 'rxjs/operators';

@Component({
  selector: 'c8y-app-plugins',
  templateUrl: './application-plugins.component.html'
})
export class ApplicationPluginsComponent implements OnInit {
  exportedPlugins$: BehaviorSubject<ApplicationPlugin[]> = new BehaviorSubject([]);
  remotePlugins$: BehaviorSubject<ApplicationRemotePlugins> = new BehaviorSubject({});
  allAvailablePlugins$: BehaviorSubject<ApplicationPlugin[]> = new BehaviorSubject([]);

  installedPlugins$: Observable<ApplicationPlugin[]> = combineLatest([
    this.remotePlugins$.pipe(map((remotes) => this.getRemotePluginsList(remotes))),
    this.allAvailablePlugins$
  ]).pipe(
    map(([remotePlugins, allPlugins]) => allPlugins.filter((p) => remotePlugins.includes(p.id))),
    shareReplay(1)
  );
  app: IApplication;
  isLoading: boolean;
  pluginsToDelete: string[] = [];
  archives: IApplicationBinary[] = [];

  constructor(
    private activatedRoute: ActivatedRoute,
    private ecosystemService: EcosystemService,
    private bsModalService: BsModalService,
    private modal: ModalService,
    private translateService: TranslateService,
    private pluginsService: PluginsService,
    private alertService: AlertService
  ) {}

  async ngOnInit() {
    const { id } = this.activatedRoute.snapshot.parent.data.contextData;
    this.app = await this.ecosystemService.getApplication(id);
    this.archives = await this.ecosystemService.listArchives(this.app.id);
    this.archives.sort((a, b) => {
      return (new Date(b.created) as any) - (new Date(a.created) as any);
    });
    const manifest: Partial<IManifest> = this.app.manifest;
    if (manifest) {
      const exports = manifest.exports || [];
      this.exportedPlugins$.next(this.extendPluginList(exports, this.app.contextPath));
      const remotes = manifest.remotes || {};
      this.remotePlugins$.next(remotes);
    }
    this.allAvailablePlugins$.next(await this.getAllPlugins());
  }

  async installPlugins() {
    const initialState: any = {
      plugins$: combineLatest([
        this.allAvailablePlugins$,
        this.installedPlugins$.pipe(map((plugins) => plugins.map((p) => p.id))),
        this.exportedPlugins$.pipe(map((plugins) => plugins.map((p) => p.id)))
      ]).pipe(
        map(([allPlugins, installedPlugins, exportedPluginNames]) => {
          const plugins = [];
          allPlugins
            .filter((p) => !exportedPluginNames.includes(p.id))
            .forEach((p) => plugins.push({ ...p, installed: !!installedPlugins.includes(p.id) }));
          return plugins;
        }),
        shareReplay(1)
      )
    };

    try {
      const pluginsToAdd: string[] = (await (
        this.bsModalService.show(InstallPluginComponent, {
          class: 'modal-md',
          initialState,
          ignoreBackdropClick: true
        }).content as InstallPluginComponent
      ).result) as any as string[];

      const currentRemotes = { ...this.remotePlugins$.value };
      pluginsToAdd.forEach((pluginId) => {
        const { contextPath, name } = this.parsePluginId(pluginId);
        (currentRemotes[contextPath] = currentRemotes[contextPath] || []).push(name);
      });
      if (this.archives.length === 6) {
        await this.ecosystemService.removeOldestArchive(this.app, this.archives);
      }
      this.updateApplicationRemotes(currentRemotes);
    } catch (ex) {
      // intended empty
    }
  }

  async removePlugins() {
    const humanizedAppName = await this.ecosystemService.getHumanizedAppName(this.app);
    try {
      await this.modal.confirm(
        gettext('Remove plugins'),
        this.translateService.instant(
          gettext(
            `You are about to remove plugins from application "{{ humanizedAppName }}". The operation may take several minutes. During this time the application may be unavailable. Do you want to proceed?`
          ),
          { humanizedAppName }
        ),
        Status.DANGER,
        {
          ok: gettext('Remove'),
          cancel: gettext('Cancel')
        }
      );

      const remotes = { ...this.remotePlugins$.value };
      this.pluginsToDelete.forEach((pluginId) => {
        const { contextPath, name } = this.parsePluginId(pluginId);
        remotes[contextPath] = remotes[contextPath].filter((p) => p !== name);
      });
      this.updateApplicationRemotes(remotes);
    } catch (ex) {
      // do nothing
    }
  }

  private async getAllPlugins(): Promise<ApplicationPlugin[]> {
    const plugins = [];
    const packages = await this.ecosystemService.getPackageApplications();
    const apps = [...packages, this.app];
    apps.forEach((app) => {
      const exports = (app.manifest && app.manifest.exports) || [];
      plugins.push(...this.extendPluginList(exports, app.contextPath));
    });
    return plugins;
  }

  private async updateApplicationRemotes(remotes: ApplicationRemotePlugins): Promise<void> {
    this.isLoading = true;
    try {
      await this.pluginsService.updateRemotesInCumulocityJson(this.app, remotes);
      this.remotePlugins$.next(remotes);
    } catch (ex) {
      this.alertService.danger(ex);
    } finally {
      this.isLoading = false;
    }
  }

  private getRemotePluginsList(remotes: ApplicationRemotePlugins): string[] {
    const importContextPaths = Object.keys(remotes);
    const plugins = [];
    importContextPaths.forEach((contextPath) => {
      const moduleNames = remotes[contextPath];
      plugins.push(...moduleNames.map((module) => this.createPluginId(contextPath, module)));
    });
    return plugins;
  }

  private extendPluginList(plugins: ApplicationPlugin[], contextPath: string): ApplicationPlugin[] {
    const extendedPlugins = [];
    plugins.map((p) => {
      extendedPlugins.push({
        ...p,
        id: this.createPluginId(contextPath, p.name, p.module),
        contextPath
      });
    });
    return extendedPlugins;
  }

  private createPluginId(contextPath: string, pluginName: string, pluginModuleName?: string): string {
    return `${contextPath}/${pluginModuleName || pluginName}`;
  }

  private parsePluginId(id: string): { contextPath: string; name: string } {
    const [contextPath, name] = id.split('/');
    return { contextPath, name };
  }
}
