import { EventEmitter, Injectable } from '@angular/core';
import { FormGroup } from '@angular/forms';
import {
  ApplicationService,
  ApplicationType,
  IApplication,
  IApplicationBinary,
  ICurrentTenant,
  IdReference,
  IFetchResponse,
  IManifest,
  IResult,
  IResultList,
  TenantService
} from '@c8y/client';
import {
  AlertService,
  AppStateService,
  gettext,
  HumanizeAppNamePipe,
  ModalService,
  Status,
  ZipService
} from '@c8y/ngx-components';
import { TranslateService } from '@ngx-translate/core';
import { saveAs } from 'file-saver/FileSaver';
import { cloneDeep, get, groupBy, kebabCase, pick } from 'lodash-es';
import { BehaviorSubject, Observable } from 'rxjs';
import { debounceTime, take } from 'rxjs/operators';
import { APP_STATE } from './ecosystem.constants';
import { ApplicationState, ERROR_TYPE } from './ecosystem.model';

const CUMULOCITY_JSON = 'cumulocity.json';

@Injectable()
export class EcosystemService {
  appDeleted = new EventEmitter<IApplication>();
  progress: BehaviorSubject<number> = new BehaviorSubject<number>(null);
  private appsGroupedByContextPath: any[];
  private xhr: XMLHttpRequest;

  constructor(
    private modal: ModalService,
    private alertService: AlertService,
    private humanizeAppName: HumanizeAppNamePipe,
    private translateService: TranslateService,
    private applicationService: ApplicationService,
    private appStateService: AppStateService,
    private zipService: ZipService,
    private tenantService: TenantService
  ) {}

  getUniqueAppConfig(srcApp: IApplication, existingApps: IApplication[]): IApplication {
    let app: IApplication = {
      name: srcApp.name,
      key: srcApp.key,
      contextPath: srcApp.contextPath
    };
    for (let retryNo = 0; retryNo < 9; ) {
      if (this.checkIfAppNameKeyPathExists(existingApps, app, retryNo)) {
        retryNo++;
        app = {
          name: [srcApp.name, retryNo].join('-'),
          key: [srcApp.key, retryNo].join('-'),
          contextPath: [srcApp.contextPath, retryNo].join('-')
        };
      } else {
        return app;
      }
    }
    return app;
  }

  getApplication(appId: IdReference): IApplication {
    return this.applicationService.detail(appId).then((res) => res.data);
  }

  getApplications(customFilter: any = {}): Promise<IResultList<IApplication>> {
    const filter: object = {
      pageSize: 2000,
      withTotalPages: true
    };
    Object.assign(filter, customFilter);
    const currentTenant = this.appStateService.currentTenant.value;
    return this.applicationService.listByTenant(currentTenant.name, filter);
  }

  async getMicroservices(): Promise<IApplication[]> {
    const apps = (await this.getApplications()).data;
    const microservices = apps.filter((app) => this.isMicroservice(app));
    return microservices.sort((a, b) => a.name.localeCompare(b.name));
  }

  async getWebApplications(customFilter: any = {}): Promise<IApplication[]> {
    const apps = (await this.getApplications(customFilter)).data;
    const webApps = apps.filter((app) => this.isApplication(app));
    this.appsGroupedByContextPath = groupBy(webApps, 'contextPath');
    return webApps.sort((a, b) => a.name.localeCompare(b.name));
  }

  async getFeatureApplications(customFilter: any = {}): Promise<IApplication[]> {
    const apps = (await this.getApplications(customFilter)).data;
    const webApps = apps.filter((app) => this.isFeature(app));
    return webApps.sort((a, b) => a.name.localeCompare(b.name));
  }

  async getPackageApplications(customFilter: any = {}): Promise<IApplication[]> {
    const apps = (await this.getApplications(customFilter)).data;
    const webApps = apps.filter((app) => this.isPackage(app));
    return webApps.sort((a, b) => a.name.localeCompare(b.name));
  }

  async isMicroserviceHostingAllowed(): Promise<boolean> {
    const { data: apps } = await this.applicationService.listByName('feature-microservice-hosting');
    return !!apps.filter(app => app.owner?.tenant?.id === 'management').length;
  }

  canOpenAppInBrowser(app: IApplication): boolean {
    const isNotAFeature = !this.isFeature(app);
    const hasProperType = [
      ApplicationType.HOSTED,
      ApplicationType.EXTERNAL,
      ApplicationType.REPOSITORY
    ].includes(app.type);
    const isNotAPackage = !this.isPackage(app);
    return isNotAFeature && hasProperType && isNotAPackage;
  }

  canDeleteApp(app: IApplication): boolean {
    return !this.isCurrentApp(app) && this.isOwner(app);
  }

  isOwner(app: IApplication): boolean {
    const currentTenant: ICurrentTenant = this.appStateService.currentTenant.value;
    const appOwner = get(app, 'owner.tenant.id');
    return currentTenant.name === appOwner;
  }

  isFeature(app: IApplication): boolean {
    return !!app.name.match(/feature-/);
  }

  isMicroservice(app: IApplication): boolean {
    return app.type === 'MICROSERVICE';
  }

  isExternal(app: IApplication): boolean {
    return app.type === 'EXTERNAL';
  }

  isPackage(app: IApplication): boolean {
    return app.manifest?.isPackage === true;
  }

  cancelAppCreation(app: IApplication): void {
    if (this.xhr) {
      this.xhr.abort();
    }
    if (app) {
      this.applicationService.delete(app);
    }
  }

  updateUploadProgress(event): void {
    if (event.lengthComputable) {
      const currentProgress = this.progress.value;
      this.progress.next(currentProgress + (event.loaded / event.total) * (95 - currentProgress));
    }
  }

  setAppActiveVersion(app: IApplication, activeVersionId: string): Promise<IApplication> {
    return this.applicationService.update({ id: app.id, activeVersionId });
  }

  getHumanizedAppName(app: IApplication): Promise<string> {
    return this.humanizeAppName.transform(app.name).pipe(debounceTime(250), take(1)).toPromise();
  }

  createConfig(app: IApplication, formGroupValue: FormGroup): Partial<FormGroup> {
    const { id, manifest } = app;
    let config = pick(formGroupValue, ['name', 'key', 'contextPath']);
    config = { ...config, setup: true, id, description: manifest.description };
    return config;
  }

  async updateAppManifest(
    application: IApplication,
    sourcePackage: IApplication
  ): Promise<{ res: IFetchResponse; data: any }> {
    const { id } = application;
    const cleanedApp = this.removeAppProperties(application);
    cleanedApp.setup = true;
    cleanedApp.manifest.isPackage = false;
    cleanedApp.manifest.source = sourcePackage.id;

    return await this.applicationService
      .binary(id)
      .updateFiles([{ path: CUMULOCITY_JSON, contents: JSON.stringify(cleanedApp) as any }]);
  }

  async listArchives(appId: string | number | IApplication): Promise<IApplicationBinary[]> {
    const filter = {
      pageSize: 100
    };
    return (await this.applicationService.binary(appId).list(filter)).data;
  }

  async deleteArchive(archive: IApplicationBinary, app: IApplication): Promise<void> {
    const humanizedArchiveName = await this.getHumanizedAppName(archive);
    try {
      await this.modal.confirm(
        gettext('Delete archive'),
        this.translateService.instant(
          gettext(
            `You are about to delete archive "{{ humanizedArchiveName }}". Do you want to proceed?`
          ),
          { humanizedArchiveName }
        ),
        Status.DANGER,
        { ok: gettext('Delete'), cancel: gettext('Cancel') }
      );
      await this.applicationService.binary(app).delete(archive.id);
      this.alertService.success(gettext('Archive deleted.'));
    } catch (ex) {
      if (ex) {
        this.alertService.danger(get(ex, 'data.message'), ex.data);
      }
      throw new Error('Cancelled');
    }
  }

  async downloadArchive(app: IApplication, archive: IApplicationBinary): Promise<void> {
    try {
      const binary = await this.getBinary(app, archive);
      const fileBinary = new Blob([binary], { type: 'application/x-zip-compressed' });
      saveAs(fileBinary, archive.name);
    } catch (e) {
      // empty
    }
  }

  async updateApp(app: IApplication, deleteOnFailure: boolean = false): Promise<IResult<IApplication>> {
    try {
      return await this.applicationService.update(app);
    } catch (ex) {
      this.alertService.addServerFailure(ex);
      if (deleteOnFailure) {
        await this.applicationService.delete(app.id);
        throw new Error('Application creation failed.');
      }
    }
  }

  async deleteApp(app: IApplication): Promise<void> {
    const humanizedAppName = await this.getHumanizedAppName(app);
    await this.modal.confirm(
      gettext('Delete application'),
      this.translateService.instant(
        gettext(
          `You are about to delete application "{{ humanizedAppName }}". Do you want to proceed?`
        ),
        { humanizedAppName }
      ),
      Status.DANGER,
      { ok: gettext('Delete'), cancel: gettext('Cancel') }
    );
    await this.applicationService.delete(app.id);
    this.alertService.success(gettext('Application deleted.'));
    this.appDeleted.emit(app);
  }

  async checkIfSubscribed(app: IApplication): Promise<boolean> {
    const currentTenant = await this.tenantService.current();
    const subscribedApps = currentTenant.data.applications.references;
    return subscribedApps.some((application) => application.application.id === app.id);
  }

  async subscribeApp(app: IApplication): Promise<void> {
    const currentTenant: ICurrentTenant = this.appStateService.currentTenant.value;
    try {
      await this.tenantService.subscribeApplication(currentTenant, app);
      this.alertService.success(gettext('Successfully subscribed to application.'));
    } catch (ex) {
      this.alertService.addServerFailure(ex);
    }
  }

  async unsubscribeApp(app: IApplication): Promise<void> {
    const currentTenant: ICurrentTenant = this.appStateService.currentTenant.value;
    try {
      await this.tenantService.unsubscribeApplication(currentTenant, app);
      this.alertService.success(gettext('Successfully unsubscribed from application.'));
    } catch (ex) {
      this.alertService.addServerFailure(ex);
    }
  }

  async isValidAppType(archive: File, appType: ApplicationType): Promise<boolean> {
    try {
      const currentType = await this.getAppType(archive);
      if (currentType !== appType) {
        throw new Error(ERROR_TYPE.TYPE_VALIDATION);
      } else {
        this.progress.next(this.progress.value + 10);
        return true;
      }
    } catch (ex) {
      throw new Error(ERROR_TYPE.TYPE_VALIDATION);
    }
  }

  async uploadArchiveToApp(archive: File, app: IApplication): Promise<IApplication> {
    const binaryService = this.applicationService.binary(app);
    this.xhr = binaryService.uploadWithProgressXhr(archive, this.updateUploadProgress.bind(this));

    const binaryMo = await binaryService.getXMLHttpResponse(this.xhr);
    return (await this.setAppActiveVersion(app, binaryMo.id as string)).data;
  }

  async createAppForArchive(archive, isPackageTypeArchive: boolean = false): Promise<IApplication> {
    let isPackage: boolean = false;
    const appType = await this.getAppType(archive);
    let appModel: any = {};

    if (appType === ApplicationType.HOSTED) {
      try {
        appModel = await this.getCumulocityJson(archive).toPromise();
        isPackage = appModel.isPackage;
      } catch (e) {
        // do nothing, we allow having HOSTED applications without the manifest file
      }
    }
    const name = this.getBaseNameFromArchiveOrAppModel(archive, appType, appModel);
    const clearedName = this.removeForbiddenCharacters(name);
    const key = this.getAppKey(appModel, clearedName);
    const contextPath = this.getContextPath(appModel, name);

    const appToSave = {
      resourcesUrl: '/',
      type: appType,
      name,
      key,
      contextPath
    };

    if (isPackageTypeArchive && !isPackage) {
      throw new Error(ERROR_TYPE.INVALID_PACKAGE);
    } else if (!isPackageTypeArchive && isPackage) {
      throw new Error(ERROR_TYPE.INVALID_APPLICATION);
    }
    return (
      await this.applicationService.create({
        ...appToSave,
        manifest: { isPackage } as unknown as IManifest
      })
    ).data;
  }

  async reactivateArchive(app: IApplication): Promise<void> {
    try {
      await this.applicationService.reactivateArchive(app.id);
      this.alertService.success(gettext('Application reactivated.'));
    } catch (ex) {
      this.alertService.addServerFailure(ex);
    }
  }

  async removeOldestArchive(app: IApplication, archives: IApplicationBinary[]): Promise<void> {
    try {
      await this.modal.confirm(
        gettext('Delete oldest archive and continue'),
        gettext(
          'Up to 6 archives can be saved in the platform. If you upload a new archive, the oldest archive that is not active will be deleted. Do you want to proceed?'
        ),
        Status.INFO,
        { ok: gettext('Delete and continue') }
      );
      const archiveToDelete = archives[archives.length - 2];
      await this.applicationService.binary(app).delete(archiveToDelete.id);
      this.alertService.success(gettext('Archive deleted.'));
    } catch (ex) {
      this.alertService.addServerFailure(ex);
    }
  }

  getAppState(app: IApplication): ApplicationState {
    if (!this.isOwner(app)) {
      return APP_STATE.SUBSCRIBED;
    } else if (this.isUnpacked(app)) {
      return APP_STATE.UNPACKED;
    } else if (app.type === ApplicationType.EXTERNAL) {
      return APP_STATE.EXTERNAL;
    } else if (this.isApplicationPackage(app)) {
      return APP_STATE.PACKAGE_APP;
    } else if (this.isPluginsPackage(app)) {
      return APP_STATE.PACKAGE_PLUGIN;
    }
    return APP_STATE.CUSTOM;
  }

  isApplicationPackage(app: IApplication): boolean {
    return this.isPackage(app) && app.manifest.package === 'application';
  }

  isPluginsPackage(app: IApplication): boolean {
    return this.isPackage(app) && app.manifest.package === 'plugin';
  }

  isUnpacked(app: IApplication): boolean {
    return !!app.manifest?.source;
  }

  hasExports(app: IApplication): boolean {
    return !!app.manifest?.exports?.length;
  }

  isApplication(app: IApplication): boolean {
    return (
      app.type !== ApplicationType.MICROSERVICE && !this.isFeature(app) && !this.isPackage(app)
    );
  }

  isCustomMicroservice(app: IApplication) {
    return this.isOwner(app) && app.type === ApplicationType.MICROSERVICE;
  }

  async isOverwrittenByCustomApp(app: IApplication): Promise<boolean> {
    if (!this.appsGroupedByContextPath) {
      await this.getWebApplications();
    }
    return (
      app.contextPath &&
      this.appsGroupedByContextPath[app.contextPath].length === 2 &&
      !this.isOwner(app)
    );
  }

  private getAppKey(appModel: IApplication, name: string): string {
    let key = appModel?.key;
    if (!key) {
      key = `${kebabCase(name)}-key`;
    }
    return key;
  }

  private getContextPath(appModel: IApplication, name: string): string {
    return appModel?.contextPath || name.toLowerCase();
  }

  private removeForbiddenCharacters(str: string): string {
    return str.replace(/[^a-zA-Z0-9-_]/g, '');
  }

  private isCurrentApp(app: IApplication): boolean {
    const currentApp = this.appStateService.state.app;
    return currentApp.contextPath === app.contextPath;
  }

  private getCumulocityJson(archive: File): Observable<any> {
    return this.zipService.getJsonData(archive, {
      filename: CUMULOCITY_JSON
    });
  }

  private getAppType(archive: File): Promise<ApplicationType> {
    return this.getCumulocityJson(archive)
      .toPromise()
      .then(
        (data) =>
          get(data, 'type') ||
          (get(data, 'apiVersion') ? ApplicationType.MICROSERVICE : ApplicationType.HOSTED)
      )
      .catch(() => ApplicationType.HOSTED);
  }

  private async getBinary(app: IApplication, archive: IApplicationBinary): Promise<ArrayBuffer> {
    let binary;
    try {
      const res = await this.applicationService.binary(app).downloadArchive(archive.id);
      binary = await res.arrayBuffer();
    } catch (ex) {
      const msg = gettext('Could not get the binary.');
      this.alertService.danger(msg);
    }
    return binary;
  }

  private getBaseNameFromArchiveOrAppModel(
    archive: any,
    appType: ApplicationType,
    appModel?: IApplication
  ): string {
    let baseName = appModel?.name || archive.name.replace(/\.zip$/i, '');
    if (appType === 'MICROSERVICE') {
      baseName = baseName.replace(/-\d+\.\d+\.\d+(-SNAPSHOT)?$/, '');
    }
    return baseName;
  }

  private checkIfAppNameKeyPathExists(existingApps: IApplication[], app: IApplication, retryNo: number): IApplication {
    return existingApps.find(
      (existingApp) =>
        existingApp.name === app.name ||
        existingApp.key === app.key ||
        existingApp.contextPath === app.contextPath ||
        existingApp.name === [app.name, retryNo].join('-') ||
        existingApp.key === [app.key, retryNo].join('-') ||
        existingApp.contextPath === [app.contextPath, retryNo].join('-')
    );
  }

  private removeAppProperties(app: IApplication): IApplication {
    const tempApp = cloneDeep(app);
    const propertiesToRemove = ['id', 'owner', 'activeVersionId'];

    propertiesToRemove.forEach(prop => delete tempApp[prop]);
    return tempApp;
  }
}
