import { Component } from '@angular/core';
import { BehaviorSubject, Observable } from 'rxjs';
import { IApplication } from '@c8y/client';
import { shareReplay, switchMap, tap } from 'rxjs/operators';
import { EcosystemService } from '../ecosystem.service';

@Component({
  selector: 'c8y-feature-list',
  templateUrl: './feature-list.component.html'
})
export class FeatureListComponent {
  reloading: boolean = false;
  reload$: BehaviorSubject<void> = new BehaviorSubject(null);

  features$: Observable<IApplication> = this.reload$.pipe(
    tap(() => (this.reloading = true)),
    switchMap(() => this.ecosystemService.getFeatureApplications()),
    tap(() => (this.reloading = false)),
    shareReplay()
  );
  listClass: string;

  constructor(private ecosystemService: EcosystemService) {}

  ngOnInit() {
    this.loadFeatures();
  }

  loadFeatures() {
    this.reload$.next();
  }
}
