import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { EcosystemService } from '../../ecosystem.service';
import { IApplication } from '@c8y/client';

@Component({
  selector: 'c8y-package-archives',
  templateUrl: './package-archives.component.html'
})
export class PackageArchivesComponent implements OnInit {
  application: IApplication;
  isLoading: boolean = false;

  constructor(private activatedRoute: ActivatedRoute, private ecosystemService: EcosystemService) {}

  async ngOnInit(): Promise<void> {
    await this.loadApplication();
  }

  async loadApplication(): Promise<void> {
    const { id } = this.activatedRoute.snapshot.parent.data.contextData;
    this.isLoading = true;
    this.application = await this.ecosystemService.getApplication(id);
    this.isLoading = false;
  }
}
