import { Component, OnInit } from '@angular/core';
import { IApplication } from '@c8y/client';
import { WizardConfig, WizardService } from '@c8y/ngx-components';
import { ModalOptions } from 'ngx-bootstrap/modal';
import { BehaviorSubject, Observable } from 'rxjs';
import { shareReplay, switchMap, tap } from 'rxjs/operators';
import { EcosystemService } from './../../ecosystem.service';

@Component({
  selector: 'c8y-application-list',
  templateUrl: './application-list.component.html'
})
export class ApplicationListComponent implements OnInit {
  reloading: boolean = false;
  reload$: BehaviorSubject<void> = new BehaviorSubject(null);

  apps$: Observable<IApplication> = this.reload$.pipe(
    tap(() => (this.reloading = true)),
    switchMap(() => this.ecosystemService.getWebApplications()),
    tap(() => (this.reloading = false)),
    shareReplay()
  );

  listClass: string;

  constructor(private ecosystemService: EcosystemService, private wizardService: WizardService) {}

  ngOnInit() {
    this.loadApplications();
  }

  loadApplications() {
    this.reload$.next();
  }

  addApplication() {
    const wizardConfig: WizardConfig = {
      headerText: 'Add Application',
      headerIcon: 'c8y-atom'
    };

    const initialState: any = {
      wizardConfig,
      id: 'uploadApplication'
    };

    const modalOptions: ModalOptions = { initialState };

    const modalRef = this.wizardService.show(modalOptions);
    modalRef.content.onClose.subscribe(() => {
      this.loadApplications();
    });
  }
}
