import { Component } from '@angular/core';
import { ApplicationType, IApplication } from '@c8y/client';
import { gettext } from '@c8y/ngx-components';
import { EcosystemService } from '../../ecosystem.service';

@Component({
  selector: 'c8y-add-web-application',
  template: `<c8y-add-application
    [headerText]="headerText"
    [headerIcon]="'upload'"
    [successText]="successText"
    [createApplicationHandler]="createApplicationHandler"
    [uploadApplicationHandler]="uploadApplicationHandler"
    [canGoBack]="true"
  ></c8y-add-application>`
})
export class AddWebApplicationComponent {
  headerText: string = gettext('Upload web application');
  successText: string = gettext('Application created');

  constructor(private ecosystemService: EcosystemService) {}

  createApplicationHandler = (f: File) => this.createApplication(f);
  uploadApplicationHandler = (f: File, app: IApplication) => this.uploadApplication(f, app);

  async createApplication(file: File): Promise<IApplication> {
    await this.ecosystemService.isValidAppType(file, ApplicationType.HOSTED);
    return this.ecosystemService.createAppForArchive(file);
  }

  async uploadApplication(file: File, app: IApplication): Promise<IApplication> {
    return this.ecosystemService.uploadArchiveToApp(file, app);
  }
}
